#AboutMe ul li {
    list-style-type: circle;
    color: black;
}

#AboutMe h2 {
    text-align: center;
    color: black;
}

#AboutMe h4 {
    text-align: left;
    color: black;
}

#AboutMe span {
    color: black;
    text-align: center;
}

#AboutMe p {
    display: inline-flex;
    vertical-align: middle;
}

.personal {
    display: flex;
    align-items: center;
}

.icon {
    margin-right: 4px;
}

#AboutMe .cv {
    height: 50px;
}

#AboutMe .quote {
    margin-top: 30px;
}

/* light and dark mode */
.light_mode_about_me {
    color: black;
    background: white;
}

.dark_mode_about_me {
    color: black;
    background: #c8c8c8;
}
